.image-button-container {
  display: flex;
  align-items: center;
  border: 2px solid black;
  border-radius: 50px;
  width: 200px;
  padding: 15px 30px;
  margin: 10px auto;
  
}

.image-button-container > img {
  width: 30px;
  margin-right: 10px;
}

.image-button-container:hover{
  background-color: #0073b0;
  color: #fff;
}